<template>
  <main>
    <span>{{ errMsg }}</span>
    <div id="Login">
      <img src="@/assets/gogame.jpg" />
      <form @submit="sendForm()">
        <input type="text" v-model="nickname" placeholder="Nickname" /><br />
        <input type="submit" value="GO" />
      </form>
    </div>
  </main>
</template>

<script>
export default {
  name: "Login",
  components: {},
  data() {
    return {
      nickname: "",
      errMsg: "",
    };
  },
  created() {
    console.log(this.$router);
  },
  methods: {
    sendForm() {
      if (this.nickname.length) {
        window.nickname = this.nickname;
        this.$router.push("/gogame");
      } else {
        this.errMsg = "Invalid Nickname";
      }
    },
  },
};
</script>

