<template>
  <main>
    <div id="GoGame">
      <h1 v-if="opponent">Your opponent is {{ opponent }}</h1>
      <h1 v-if="errMsg">{{ errMsg }}</h1>
      <h1 v-else-if="gameOver">Game Over : {{ gameOver }}</h1>
      <h1 v-else-if="round == 1">Black's Round</h1>
      <h1 v-else-if="round == 2">White's Round</h1>
      <h1 v-else>Waiting for new player...</h1>
      <GoBoard />
    </div>
  </main>
</template>

<script>
import GoBoard from "@/components/GoBoard.vue";
export default {
  name: "GoGame",
  components: {
    GoBoard,
  },
  data() {
    return {
      round: 0,
      gameOver: "",
      errMsg: "",
      opponent: "",
    };
  },
  mounted() {
    window.socket.on("gameOver", (data) => {
      //console.log(data);
      if (data == -1 && this.gameOver == "")
        this.gameOver = "Your opponent has disconnected";
      else if (data == 1) this.gameOver = "Black Win";
      else if (data == 2) this.gameOver = "White Win";
    });
    window.socket.on("round", (data) => {
      this.round = data;
    });
    window.socket.on("color", (data) => {
      if (data == -1) this.errMsg = "Invalid nickname";
      else if (data == -2) this.errMsg = "The nickname has been used";
    });
    window.socket.on("nickname", (data) => {
      this.opponent = data;
    });
    window.socket.emit("nickname", window.nickname);
  },
};
</script>

