  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#GoGame {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

main {
  font-size: 1vw;
}
